.wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
}

.userCard {
  border: 1px solid lightgrey;
  border-radius: 8px;
  padding: 20px 40px;
}

.imageCard {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.userImageHolder {
  min-width: 160px;
  height: 120px;
}

.userImage {
  width: 120px;
  height: 120px;
  border-radius: 20px;
}

.imageTopCornerButton {
  position: relative;
  right: 20px;
  width: fit-content;
  background-color: #1c8090;
  padding: 5px;
  color: white;
  font-size: 18px;
  border-radius: 8px;
  cursor: pointer;
}

.userTextInfoHolder {
  flex-grow: 2;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

h1,
h4 {
  margin: 0;
}

button {
  background-color: #1c8090;
  border: none;
  color: white;
  padding: 8px 20px;
  width: fit-content;
  border-radius: 6px;
}

.profileFields {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.profileField {
  width: 60%;
  font-size: 16px;
  font-weight: 100;
  height: auto;
  padding: 8px 15px;
  border-radius: 8px;
  border: solid 1px #003873;
  background-color: #fff;
  color: grey;
}

@media (max-width: 700px) {
  .imageCard {
    flex-direction: column;
  }

  h1 {
    font-size: 24px;
  }
}
